import React, { useEffect, useRef, useState } from 'react';
import JsSIP from 'jssip';

const SIPPhone = () => {
  const [session, setSession] = useState(null);
  const [isMuted, setIsMuted] = useState(false);
  const [isCalling, setIsCalling] = useState(false);
  const [isInCall, setIsInCall] = useState(false);
  const sessionRef = useRef(null);

  useEffect(() => {
    const socket = new JsSIP.WebSocketInterface('wss://10.30.250.50:8089/ws');
    const configuration = {
      sockets: [socket],
      uri: 'sip:9001@10.30.250.50',
      password: 'your_password_here', // change this!
    };

    const ua = new JsSIP.UA(configuration);
    ua.start();

    ua.on('newRTCSession', (data) => {
      const newSession = data.session;

      if (sessionRef.current) {
        console.log('Already in session, rejecting new one');
        newSession.terminate();
        return;
      }

      sessionRef.current = newSession;
      setSession(newSession);

      if (data.originator === 'remote') {
        newSession.answer({
          mediaConstraints: { audio: true, video: false },
        });
      }

      newSession.on('ended', () => {
        console.log('Call ended');
        sessionRef.current = null;
        setSession(null);
        setIsCalling(false);
        setIsInCall(false);
        setIsMuted(false);
      });

      newSession.on('accepted', () => {
        console.log('Call accepted');
        setIsCalling(false);
        setIsInCall(true);
        setIsMuted(false);
      });

      newSession.on('failed', () => {
        console.log('Call failed');
        sessionRef.current = null;
        setSession(null);
        setIsCalling(false);
        setIsInCall(false);
        setIsMuted(false);
      });

      newSession.on('muted', () => {
        console.log('Muted');
      });

      newSession.on('unmuted', () => {
        console.log('Unmuted');
      });
    });

    return () => {
      ua.stop();
    };
  }, []);

  const handleCall = () => {
    const eventHandlers = {
      progress: () => console.log('Call in progress'),
      failed: (e) => console.log('Call failed', e),
      ended: () => {
        console.log('Call ended');
        sessionRef.current = null;
        setIsInCall(false);
        setIsMuted(false);
      },
      confirmed: () => console.log('Call confirmed'),
    };

    const options = {
      eventHandlers,
      mediaConstraints: { audio: true, video: false },
    };

    const newSession = sessionRef.current = window.ua.call('sip:9008@10.30.250.50', options);
    setSession(newSession);
    setIsCalling(true);
  };

  const handleHangup = () => {
    if (sessionRef.current) {
      sessionRef.current.terminate();
      sessionRef.current = null;
    }
    setSession(null);
    setIsCalling(false);
    setIsInCall(false);
    setIsMuted(false);
  };

  const handleMuteToggle = () => {
    if (!sessionRef.current) return;

    if (isMuted) {
      sessionRef.current.unmute({ audio: true });
    } else {
      sessionRef.current.mute({ audio: true });
    }

    setIsMuted(!isMuted);
  };

  return (
    <div style={{ padding: '20px', fontFamily: 'sans-serif' }}>
      <h2> WebRTC SIP Phone</h2>

      {!isInCall && (
        <button onClick={handleCall} disabled={isCalling}>
          {isCalling ? 'Calling...' : 'Call'}
        </button>
      )}

      {isInCall && (
        <>
          <button onClick={handleHangup}>Hangup</button>
          <button onClick={handleMuteToggle}>
            {isMuted ? ' Unmute' : ' Mute'}
          </button>
        </>
      )}
    </div>
  );
};

export default SIPPhone;
